<template>
	<div>
		<tabs>
			<tab title="preview">
				
        <simple-table ref="stbl" 
          :data="data" :header="header" :config="config"
          @edit_row_emit="edit_row_emit">
          <div slot="edit_row">
            <pre v-highlightjs><code class="javascript">{{ current_edit_row }}</code></pre>
            <p><button @click.prevent="$refs.stbl.cancel_edit_row()">Cancel</button></p>
          </div>
				</simple-table>

			</tab>

			<tab title="html">
				<pre v-highlightjs="code"><code class="javascript"></code></pre>
			</tab>

			<tab title="script">
				<pre v-highlightjs="code_js"><code class="javascript"></code></pre>
			</tab>

      <tab title="event">
        <simple-table 
          :config="{table_class: 'table margin-0'}"
          :header="[{title: 'Event'}, {title: 'Return'}, {title: 'Description'}]"
          :data="[{event: 'edit_row_emit', return: 'Object', description: 'When click edit this event will return current row data'}]">
        </simple-table>
      </tab>

      <tab title="slot">
        <simple-table 
          :config="{table_class: 'table margin-0'}"
          :header="[{title: 'Slot'}]"
          :data="[{slot: 'edit_row'}]">
        </simple-table>
      </tab>

      <tab title="ref">
        <simple-table 
          :config="{table_class: 'table margin-0'}"
          :header="[{title: 'Method'}, {title: 'Ref'}]"
          :data="[{method: 'cancel_edit_row', use: '$refs.stbl.cancel_edit_row'}]">
        </simple-table>
        eg:
        <pre v-highlightjs><code>&lt;simple-table ref="stbl"&gt;&lt;/simple-table&gt;</code></pre>
      </tab>
		</tabs>
	</div>
</template>

<script>
	import { Tabs, Tab } from 'uiv'

	export default {
		name: 'config-edit_row',
		data() {
			return {
				config: {
					edit_row: true,
					table_class: 'table margin-0'
				},

				code: `<simple-table ref="stbl" :data="data" :header="header" :config="config">
  <div slot="edit_row">
    <p><button @click.prevent="$refs.stbl.cancel_edit_row()">Cancel</button></p>
  </div>
</simple-table>`,
        code_js: `export default {
    data() {
      return {

        config: {
          edit_row: true,
          table_class: 'table margin-0'
        },

        header: [
          {"title": "id" },
          {"title": "title" },
          {"title": "by"}
        ],
        data: [
          {
            "id": 1,
            "title": "Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
            "author": "administrator"
          },
          {
            "id": 2,
            "title": "Sed hendrerit ex quis erat commodo condimentum.",
            "author": "moderator"
          },
          {
            "id": 3,
            "title": "Maecenas egestas ligula et est accumsan suscipit.",
            "author": "guest"
          }
        ]
      }
    }
  }`,

				header: [ {"title": "id" }, {"title": "title" }, {"title": "by"} ],
        data: [
          {
            "id": 1,
            "title": "Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
            "author": "administrator"
          },
          {
            "id": 2,
            "title": "Sed hendrerit ex quis erat commodo condimentum.",
            "author": "moderator"
          },
          {
            "id": 3,
            "title": "Maecenas egestas ligula et est accumsan suscipit.",
            "author": "guest"
          }
        ],

        current_edit_row: {}

			}
		},

    methods: {
      edit_row_emit(row) {
        this.current_edit_row = JSON.parse(JSON.stringify(row))
        // row.title = `changed id: ${row.id}`
      }
    },

		components: {
			'simple-table': require('@/components/vue-simple-table.vue'),
			Tabs, Tab
		}
	}
</script>


