<template>
	<simple-table :header="header" :data="data"></simple-table>
</template>

<script>
	export default {
		name: 'basic',

		data() {
			return {
				header: [
	        { title: 'one' },
	        { title: 'two' }
	      ],

	      data: [
	        { id: 1, x: 'Lorem ipsum dolor sit amet' },
	        { id: 2, y: 'consectetur adipisicing elit' },
	        { id: 3, z: 'In rerum voluptatem' }
	      ]
			}
		},

		components: {
			'simple-table': require('@/components/vue-simple-table.vue')
		}
	}
</script>


