import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'

Vue.use(Router)

export default new Router({
  routes: [

    { path: '/', name: 'full', component: require('@/components/full') },

    { path: '/prop', name: 'prop', component: require('@/components/ex/prop/prop') },
    { path: '/prop/config', name: 'prop_config', component: require('@/components/ex/prop/config') },
    { path: '/prop/header', name: 'prop_header', component: require('@/components/ex/prop/header') },
    { path: '/prop/body', name: 'prop_body', component: require('@/components/ex/prop/body') },

    { path: '/basic', name: 'Basic', component: require('@/components/ex/basic') },

    // { path: '/sort', name: 'Sort', component: require('@/components/ex/sort') },

    // { path: '/checkbox', name: 'Checkbox', component: require('@/components/ex/checkbox') }

  ]
})



