<template>
  <div class="margin-top">

    <h2>Table header</h2>

    <div class="margin">
      <Tabs>
        <tab title="Preview">
          <simple-table :header="header" :data="data" :config="{sort: true}"></simple-table>
        </tab>
        <tab title="HTML">
          <pre v-highlightjs="code"><code class="javascript"></code></pre>
        </tab>
        <tab title="SCRIPT">
          <pre v-highlightjs><code class="javascript">
  export default {
    data() {
      return {
        header: [
          {
            "title": "id",
            "key": "id"
          },
          {
            "title": "title",
            "key": "title"
          },
          {
            "title": "category name",
            "key": "category"
          },
          {
            "title": "create at ...",
            "key": "create"
          },
          {
            "title": "by",
            "key": "author"
          }
        ],
        data: [
          {
            "id": 1,
            "title": "Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
            "category": "Internet",
            "create": "2017-02-03",
            "author": "administrator"
          },
          {
            "id": 2,
            "title": "Sed hendrerit ex quis erat commodo condimentum.",
            "category": "Cooking",
            "create": "2017-09-15",
            "author": "moderator"
          },
          {
            "id": 3,
            "title": "Maecenas egestas ligula et est accumsan suscipit.",
            "category": "Default",
            "create": "2016-11-21",
            "author": "guest"
          }
        ]
      }
    }
  }</code></pre>
        </tab>
      </Tabs>
    </div>

    <div class="panel panel-default">
      <div class="panel-heading">Prop config</div>
      <div class="panel-body padding-0">
        <table class="table table-bordered margin-0">
          <tbody>
            <tr>
              <th>Property</th>
              <th>Value</th>
            </tr>
            <tr>
              <td>sort</td>
              <td>Boolean</td>
            </tr>
            <tr>
              <td colspan="2">
                <u>Note</u>: property <kbd>key</kbd> in prop header <code>==</code> 
                property <kbd>name</kbd> in prop data
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

  </div>
</template>

<script>
import { Tabs, Tab } from 'uiv'

  export default {
    name: 'sort',
    data () {

      return {

        code: `<simple-table :header="header" :data="data" :config="{sort: true}"></simple-table>`,

        header: [
          { title: 'id', key: 'id'},
          { title: 'title', key: 'title'},
          { title: 'category name', key: 'category'},
          { title: 'create at ...', key: 'create'},
          { title: 'by', key: 'author'}
        ],

        data: [
          {
            id: 1,
            title: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.',
            category: 'Internet',
            create: '2017-02-03',
            author: 'administrator'
          },
          {
            id: 2,
            title: 'Sed hendrerit ex quis erat commodo condimentum.',
            category: 'Cooking',
            create: '2017-09-15',
            author: 'moderator'
          },
          {
            id: 3,
            title: 'Maecenas egestas ligula et est accumsan suscipit.',
            category: 'Default',
            create: '2016-11-21',
            author: 'guest'
          }
        ]
      }
    },

    components: {
      'simple-table': require('@/components/vue-simple-table.vue'),
      Tabs, Tab
    }
  }
</script>


