<template>
	<div class="my-sidenav">
		<slot></slot>
	</div>
</template>

<style>
	.my-sidenav {
    width: 200px;
    position: fixed;
    top: 0;
    left: 0;
    margin-left: 30px;
    margin-top: 30px;
	}
</style>

<script>
	export default {
		name: 'my-sidenav'
	}
</script>


