<template>
	<div>
		<tabs>
      <tab title="Preview">
        <simple-table v-bind="{header, data, config}"></simple-table>
      </tab>
      <tab title="html">
        <pre v-highlightjs="code"><code class="javascript"></code></pre>
      </tab>
      <tab title="script">
        <pre v-highlightjs><code class="javascript">export default {
  data () {
    return {

      config: {
        table_class: 'table table-hover',
        head_class: 'bg-primary', 
        body_class: 'well'
      },

      header: [
        {title: 'Id'},
        {title: 'Title'}
      ],

      data: [
        {
          id: 1,
          title: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.'
        },
        {
          id: 2,
          title: 'Sed hendrerit ex quis erat commodo condimentum.'
        }
      ]
    }
  }
}</code></pre>
      </tab>
    </tabs>
	</div>
</template>

<script>
import { Tabs, Tab } from 'uiv'

  export default {
    name: 'table-classes',
    data () {
      return {
        code: `<simple-table :header="header":data="data" :config="config"></simple-table>`,

        config: {
          table_class: 'table table-hover margin-0',
          head_class: 'bg-primary', 
          body_class: 'well'
        },

        header: [
          {title: 'Id'},
          {title: 'Title'}
        ],

        data: [
          {
            id: 1,
            title: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.'
          },
          {
            id: 2,
            title: 'Sed hendrerit ex quis erat commodo condimentum.'
          }
        ]
      }
    },

    components: {
      'simple-table': require('@/components/vue-simple-table.vue'), 
      Tabs, Tab,
    }
  }
</script>


