<template>
  <div>

    <div>
      
      <tabs>
        <tab title="Preview">
          <simple-table 
            :header="header" 
            :data="data" 
            :config="{checkbox: true, table_class: 'table'}" 
            @get_checkbox="get_checkbox">
          </simple-table>
          <pre class="margin-top">{{ JSON.stringify(checkbox) }}</pre>
        </tab>
        <tab title="html">
          <pre v-highlightjs><code class="javascript">{{ html }}</code></pre>
        </tab>
        <tab title="javascript">
          <pre v-highlightjs><code class="javascript">export default {

    data () {
      return {

        checkbox: [],

        header: [
          { title: 'checkbox hidden title' },
          { title: 'two'}
        ],

        data: [
          {
            id: 1,
            title: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.'
          },
          {
            id: 2,
            title: 'Sed hendrerit ex quis erat commodo condimentum.'
          },
          {
            id: 3,
            title: 'Maecenas egestas ligula et est accumsan suscipit.'
          }
        ]
      }
    },

    methods: {
      get_checkbox(checked_array) {
        this.checkbox = checked_array
      }
    }
  }</code></pre>
        </tab>
        <tab title="prop">
          <simple-table 
            :header="[{title: 'Prop'}, {title: 'Property'}, {title: 'Value'}, {title: 'Default'}]" 
            :data="[{prop: 'config', property: 'checkbox', value: 'Boolean', default: 'false'}]" 
            :config="{table_class: 'table margin-0'}">
          </simple-table>
        </tab>
        <tab title="event">
          <simple-table 
            :header="[{title: 'Event'}, {title: 'Return'}, {title: 'Description'}]" 
            :data="[{title: 'get_checkbox', return: 'Array', description: 'Array contains id'}]" 
            :config="{table_class: 'table margin-0'}">
          </simple-table>
        </tab>
      </tabs>
          
    </div>

  </div>
</template>

<!-- Event: get_checkbox = return [x,x, ...] -->

<script>
import { Tabs, Tab } from 'uiv'

  export default {
    name: 'checkbox',
    data () {
      return {

        html: `<simple-table 
  :header="header" 
  :data="data" 
  :config="{checkbox: true}" 
  @get_checkbox="get_checkbox">
</simple-table>`,

        tabIndex: 0,

        checkbox: [],

        header: [
          { title: 'checkbox hidden title' },
          { title: 'two'}
        ],

        data: [
          {
            id: 1,
            title: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.'
          },
          {
            id: 2,
            title: 'Sed hendrerit ex quis erat commodo condimentum.'
          },
          {
            id: 3,
            title: 'Maecenas egestas ligula et est accumsan suscipit.'
          }
        ]
      }
    },

    methods: {
      get_checkbox(checked_array) {
        this.checkbox = checked_array
      }
    },

    components: {
      'simple-table': require('@/components/vue-simple-table.vue'),
      Tabs, Tab
    }
  }
</script>


