<template>
	<div>
		<h3>Prop</h3>

		<div class="panel panel-default">
			<div class="panel-heading"><h4>Prop</h4></div>
			<div class="panel-body padding-0">
				<my-table
					:header="[
						{title: 'Props name', key: 'prop', class: 'pointer'}, 
						{title: 'type', key: 'type', class: 'pointer'}, 
						{title: 'description'}
					]"
					:data="[
						{prop: 'config', type: 'array', description: 'object contains the functions used and classes for table, thead, tbody'},
						{prop: 'header', type: 'array', description: 'array object for title, key, class on the element'},
						{prop: 'body', type: 'array', description: 'array contains object declare class, event and slot on td element'},
						{prop: 'data', type: 'array', description: 'array object data for render tbody'},
						{prop: 'hidden_column', type: 'array', description: 'array contains index column'},
						{prop: 'h', type: 'number', description: 'horizontal index'},
						{prop: 'v', type: 'number', description: 'vertical index'},
						{prop: 'contextmenu_edit_text', type: 'string', description: 'custom edit text on context menu'},
						{prop: 'contextmenu_remove_text', type: 'string', description: 'custom remove text on context menu'}
					]"
					:body="[
						{},
						{class: 'text-primary'}
					]"
					:config="{table_class: 'table margin-0', sort: true}">
				</my-table>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'config',

		components: {
			'my-table': require('@/components/vue-simple-table')
		}
	}
</script>


