<template>
  <div>
    <div>
      <Tabs>
        <tab title="preview">
          <simple-table :header="header" :data="data" :config="{sort: true, table_class: 'table'}"></simple-table>
        </tab>
        <tab title="html">
          <pre v-highlightjs="code"><code class="javascript"></code></pre>
        </tab>
        <tab title="script">
          <div>
            <p class="text-danger">
              Important: To use the sort <b>header property key</b> must have the same name as the <b>data property</b> eg:
            </p>
            <pre>header: [
  {title: 'Abc', key: <b>'test'</b>}, 
  {title: 'Def', key: <b>'test_2'</b>}
]
data: [
  {
    <b>test</b>: 'Lorem ip sum',
    <b>test_2</b>: 'Lorem ip dolor'
  }
]</pre>
          </div>
          <pre v-highlightjs><code class="javascript">
  export default {
    data() {
      return {
        header: [
          {
            "title": "id",
            "key": "id"
          },
          {
            "title": "title",
            "key": "title"
          },
          {
            "title": "category name",
            "key": "category"
          },
          {
            "title": "create at ...",
            "key": "create"
          },
          {
            "title": "by",
            "key": "author"
          }
        ],
        data: [
          {
            "id": 1,
            "title": "Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
            "category": "Internet",
            "create": "2017-02-03",
            "author": "administrator"
          },
          {
            "id": 2,
            "title": "Sed hendrerit ex quis erat commodo condimentum.",
            "category": "Cooking",
            "create": "2017-09-15",
            "author": "moderator"
          },
          {
            "id": 3,
            "title": "Maecenas egestas ligula et est accumsan suscipit.",
            "category": "Default",
            "create": "2016-11-21",
            "author": "guest"
          }
        ]
      }
    }
  }</code></pre>
        </tab>
        <tab title="prop">
          <simple-table
            :header="[{title: 'Property'}, {title: 'Value'}, {title: 'Default'}]"
            :data="[{property: 'sort', value: 'Boolean', default: 'false'}]"
            :config="{table_class: 'table margin-0'}">
          </simple-table>
        </tab>
      </Tabs>
    </div>

  </div>
</template>

<script>
import { Tabs, Tab } from 'uiv'

  export default {
    name: 'prop-sort',
    data () {

      return {

        code: `<simple-table :header="header" :data="data" :config="{sort: true}"></simple-table>`,

        header: [
          { title: 'id', key: 'id'},
          { title: 'title', key: 'title'},
          { title: 'category name', key: 'category'},
          { title: 'create at ...', key: 'create'},
          { title: 'by', key: 'author'}
        ],

        data: [
          {
            id: 1,
            title: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.',
            category: 'Internet',
            create: '2017-02-03',
            author: 'administrator'
          },
          {
            id: 2,
            title: 'Sed hendrerit ex quis erat commodo condimentum.',
            category: 'Cooking',
            create: '2017-09-15',
            author: 'moderator'
          },
          {
            id: 3,
            title: 'Maecenas egestas ligula et est accumsan suscipit.',
            category: 'Default',
            create: '2016-11-21',
            author: 'guest'
          }
        ]
      }
    },

    components: {
      'simple-table': require('@/components/vue-simple-table.vue'),
      Tabs, Tab
    }
  }
</script>


